'use client';
import { Search, Menu, Phone, User, ShoppingCart } from "lucide-react"
import Link from "next/link"
import {useState} from "react";
import CategoryMenu from "@/components/header/CategoryMenu";

export default function Header({groups=[]}) {
    const [showMenu, setShowMenu] = useState(false)

    return (
        <header className="w-ful">
            {/* Top promotional banner */}
            <div className="bg-red-600 text-white py-2 px-4">
                <div className="container mx-auto flex flex-col md:flex-row justify-between items-center">
                    <div className="flex items-center mb-2 md:mb-0">
                        <span className="text-yellow-300 font-bold mr-2">Mua online VƯỢT TRỘI</span>
                        <span className="text-white">DỊCH VỤ</span>
                    </div>
                    <div className="flex flex-wrap items-center justify-center">
                        <span className="text-yellow-300 font-bold mr-2">Ưu đãi đặc biệt!</span>
                        <span className="text-white">Ghế phòng họp - Bàn làm việc</span>
                        <button className="bg-blue-500 text-white rounded-full px-4 py-1 ml-4 font-bold">ƯU ĐÃI SỐC!</button>
                        <button className="bg-white text-black rounded-full px-4 py-1 ml-2 font-bold">XEM NGAY</button>
                    </div>
                </div>
            </div>

            {/* Main navigation */}
            <div className="bg-red-600 text-white py-3 px-4 border-t border-red-500 sticky top-0 z-50">
                <div className="container mx-auto flex flex-col md:flex-row items-center justify-between">
                    <div className="flex items-center mb-3 md:mb-0">
                        <div className="bg-white rounded-full h-10 w-10 flex items-center justify-center mr-2">
                            <span className="text-xs text-gray-600">Logo</span>
                        </div>
                        <span className="text-yellow-300 text-xl font-bold">CarGear</span>
                        <button className="ml-4 flex items-center"
                                onMouseMove={()=>setShowMenu(true)}>
                            <Menu className="h-6 w-6 mr-1" />
                            <span className="font-medium">Danh mục</span>
                        </button>
                        {showMenu && (
                            <div
                                onMouseLeave={()=>setShowMenu(false)}
                                className="absolute top-16 left-0 bg-white  shadow-lg w-[800px] flex border z-50">
                                <CategoryMenu groups={groups} />
                            </div>
                        )}
                    </div>
                    {/* Search bar */}
                    <div className="relative flex-1 max-w-xl mx-4 w-full mb-3 md:mb-0 ">
                        <input type="text" placeholder="Bạn tìm gì..." className="w-full py-2 px-4 rounded-full text-black bg-white" />
                        <button className="absolute right-2 top-1/2 transform -translate-y-1/2">
                            <Search className="h-5 w-5 text-gray-500" />
                        </button>
                    </div>

                    {/* Contact and account */}
                    <div className="flex items-center space-x-4">
                        <div className="flex items-center">
                            <Phone className="h-5 w-5 mr-1" />
                            <span>0974841508</span>
                        </div>
                        <div className="flex items-center">
                            <User className="h-5 w-5 mr-1" />
                            <span>Đăng nhập</span>
                        </div>
                        <div className="flex items-center">
                            <ShoppingCart className="h-5 w-5 mr-1" />
                            <span>Giỏ hàng</span>
                        </div>
                    </div>
                </div>
            </div>

            {/* Secondary navigation */}
            <nav className="bg-white text-black shadow-md overflow-x-auto">
                <div className="container mx-auto flex justify-center items-center">
                    <Link href="/" className="px-4 py-3 text-red-600 hover:bg-gray-100 whitespace-nowrap">
                        Trang chủ
                    </Link>
                    <Link href="/about" className="px-4 py-3 text-red-600 hover:bg-gray-100 whitespace-nowrap">
                        Giới thiệu
                    </Link>
                    <Link href="/tin-tuc" className="px-4 py-3 text-red-600 hover:bg-gray-100 whitespace-nowrap">
                        Tin tức
                    </Link>
                    <Link href="/san-pham" className="px-4 py-3 text-red-600 hover:bg-gray-100 whitespace-nowrap">
                        Sản phẩm
                    </Link>
                    <Link href="/lien-he" className="px-4 py-3 text-red-600 hover:bg-gray-100 whitespace-nowrap">
                        Liên hệ
                    </Link>
                </div>
            </nav>

        </header>
    )
}
